import { combineReducers } from "redux";
import { hashById } from "../../Utils/utils";

const initialState = {
  name: "",
  lastName: "",
  email: "",
  password: "",
};

const userInfo = (state = initialState, action) => {
  switch (action.type) {
    case "LOG_IN":
      return action.payload /* password : action.payload.password, email : action.payload.email} */;
    case "RECEIVE_USERINFO":
      return hashById(action.userInfo);

    case "CREATE_USER":
      return {
        ...state,
        [action.plainEventObject.id]: action.plainEventObject,
      };
    case "UPDATE_USERINFO":
      return {
        ...state,
        [action.userInfo.id]: action.userInfo,
      };

    default:
      return state;
  }
};
export const selectUser = (state) => state.userInfo; //this is an example of selectors to help you
/// extract state's value , this method uses react hooks (useSelector) to be called in the componenent/// the alternative in class based componenents is mapStateToprops
export default userInfo;
