import React, { useState } from "react";
import { connect } from "react-redux";
import { useHistory, withRouter } from "react-router";
import actionCreators from "../../Redux-Actions/Calendar-Actions/actions";
import "./TestForm.css";
const TestForm = (props) => {
  let myFormRef = React.createRef();
  let eventGuid = 1;
  let history = useHistory();
  let [display, setDisplay] = useState(false);
  function createEventId() {
    return String(eventGuid++);
  }
  let eventObj = {
    id: "",
    title: "",
    date: "",
    start: "",
    end: "",
    extendedProps: { nom: "", prenom: "" },
    //url: "",
    overlap: false,
  };
  const [eventForm, setEventForm] = useState(eventObj); //  should be uncommented ( testing for the reset button )
  const [titre, setTitre] = useState("");
  const [start, setStart] = useState("");
  const [end, setEnd] = useState("");
  const resetForm = () => {
    setTitre("");
    setStart("");
    setEnd("");
    setEventForm({});
  };

  const handleTitre = (e) => {
    let value = e.target.value;
    setTitre(value);
  };
  const handleStart = (e) => {
    let value = e.target.value;
    setStart(value);
  };
  const handleEnd = (e) => {
    let value = e.target.value;
    setEnd(value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    props.formHandler(display);
    let newEvent = {
      title: titre, //.toLocaleString(),
      start: start, //.toLocaleString(),
      end: end, //.toLocaleString(),
    };

    props.createEvent(newEvent);
    setEventForm({});
  };
  return (
    <div className="container">
      <form
        className="form"
        onSubmit={(e) => handleSubmit(e)}
        ref={(el) => (myFormRef = el)}
      >
        <div className="input_title">
          <label className="input_label">Titre : </label>

          <input
          className="input_field"
            value={titre}
            type="text"
            onChange={(e) => handleTitre(e)}
            placeholder="titre"
          />
        </div>
        <div className="input_start">
          <label className="input_label" >Start :</label>

          <input
          className="input_field"
            value={start}
            type="datetime-local"
            onChange={(e) => handleStart(e)}
            placeholder="start"
          />
        </div>
        <div className="input_end">
          <label className="input_label" >End :</label>

          <input
          className="input_field"
            type="datetime-local"
            value={end}
            onChange={(e) => handleEnd(e)}
            placeholder="end"
          />
        </div>
        <div className="submit">
          <button type="submit" className="button">
            save
          </button>
          <button className="button" onClick={resetForm} type="button">
            Cancel
          </button>
        </div>
      </form>
    </div>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    createEvent: () => dispatch(createEvent()),
  };
};

export default withRouter(
  connect(mapDispatchToProps, actionCreators)(TestForm)
);

