import React, { useState } from "react";
import { withRouter } from "react-router";
import espritLogo from "../../Assets/images/espritLogo.png";
import "./LoginForm.css";
import useractionscreators from "../../Redux-Actions/UserInfo-Actions/UserInfo-Actions";
import { connect } from "react-redux";
const LoginForm = (props) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    props.requestLogIn(email, password);
  };
  return (
    <div className="login__form">
      <img src={espritLogo} alt="logoEsprit" />
      <form onSubmit={(e) => handleSubmit(e)}>
        <input
          value={email}
          placeholder="Email"
          type="email"
          onChange={(e) => {
            setEmail(e.target.value);
          }}
        />
        <input
          value={password}
          placeholder="password"
          type="password"
          onChange={(e) => {
            setPassword(e.target.value);
          }}
        />
        <button type="submit">Sign In</button>
      </form>
      <p>
        Not a Member? {"  "}
        <span className="login_register">Register Now </span>
      </p>
    </div>
  );
};
