import {
  requestUserCreate,
  requestUser,
} from "../../Middleware/UserInfo-Middleware/requests-UserInfo";

export default  {
  requestLogIn(email,password) {
    return (dispatch) => {
      return requestUser(email,password).then(
      (userInfo) =>  dispatch({
          type: "LOG_IN",
       payload : userInfo
        })
      );
    };
  },
  requestLogOut() {
    return (dispatch) => {
      return requestUser().then(
        dispatch({
          type: "LOG_OUT",
          userInfo: null,
        })
      );
    };
  },

  requestUserInfo() {
    return (dispatch) => {
      return requestUser().then((userInfo) => {
        dispatch({
          type: "RECEIVE_EVENTS",
          userInfo,
        });
      });
    };
  },

  createUser(userInfo) {
    return (dispatch) => {
      return requestUserCreate(userInfo).then(() => {
        dispatch({
          type: "CREATE_EVENT",
          userInfo: {
            ...userInfo,
          },
        });
      });
    };
  },

  updateUser(userInfo) {
    return (dispatch) => {
      return requestUserInfoUpdate(userInfo).then(() => {
        dispatch({
          type: "UPDATE_EVENT",
          plainEventObject,
        });
      });
    };
  },
};

