export function requestEventUpdate(plainEventObject) {
  //  let events = axios.get("http://localhost:8081/getevent").then(response=> response.data.map(obj => obj))

  console.log("[STUB] requesting event update:", "events");

  return new Promise((resolve, reject) => {
    setTimeout(() => {
      if (simulateErrors) {
        reject(new Error("problem"));
      } else {
        axios.put(
          `http://localhost:8081/event/${plainEventObject.id}`,
          plainEventObject
        );
        console.log(plainEventObject.id);
        let newEventDb = [];
        newEventDb = [...newEventDb, eventDb];
        newEventDb = excludeById(newEventDb, plainEventObject.id);
        eventDb = newEventDb;
        eventDb.push(plainEventObject);
        resolve(eventDb);
      }
    }, DELAY);
  });
}

export function requestEventDelete(eventId) {
  console.log("[STUB] requesting event delete, id:", eventId);

  return new Promise((resolve, reject) => {
    setTimeout(() => {
      // simulate network delay
      if (simulateErrors) {
        reject(new Error("problem"));
      } else {
        let eventDateBase = [...eventDateBase, eventDb];
        eventDb = excludeById(eventDateBase, eventId);
        resolve(eventDateBase);
      }
    }, DELAY);
  });
}

