import { combineReducers } from "redux";
import { hashById } from "../../Utils/utils";
import userInfo from '../UserInfo-Reducer/userReducer';
export default combineReducers({
  eventsById , userInfo
});

function eventsById(eventsById = {}, action) {
  switch (action.type) {
    case "RECEIVE_EVENTS":
      return hashById(action.plainEventObjects);

    case "CREATE_EVENT":
        return {...eventsById,[action.plainEventObject.id] :action.plainEventObject}
    case "UPDATE_EVENT":
      return {
        ...eventsById,
        [action.plainEventObject.id]: action.plainEventObject,
      };

    default:
      return eventsById;
  }
}


