import React, { useState } from "react";
import Pageheader from "../Header/Header";
import SideBar from "../SideBar/SideBar";
import "./Home.css";
import { Route, Switch, withRouter, BrowserRouter } from "react-router-dom";

import DemoApp from "../Calendar/DemoApp";
import TestForm from "../TestForm/TestForm";
import LoginForm from "../Login/LoginForm";
import { useSelector } from "react-redux";
import { createSelector } from "reselect";
import { selectUser } from "../../Redux-Reducers/UserInfo-Reducer/userReducer";
import ProgEtudiant from "../ProgEtudiant/ProgEtudiant";
const Home = (props) => {
  const user = useSelector(selectUser);
  const [open, setOpen] = useState(true);
  const handleClose = () => {
    setOpen(false);
  };
  const handleOpen = () => {
    setOpen((prev) => (prev = !prev));
  };
  console.log(user);
  return (
    <div className="home">
      {/*  { !user ? ( */}

      <Pageheader SidebarIcon={handleOpen} />
      <Route exact path="/" component={LoginForm} />
      <div className="home_page">
        <SideBar show={open} modalClosed={handleClose} />
        <div className="home_page_main">
          <Switch>
            <Route path="/form" component={TestForm} />
            <Route path="/calendar" component={DemoApp} />
          </Switch>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Home);

