import {
  requestEventsInRange,
  requestEventCreate,
  requestEventUpdate,
} from "../../Middleware/Calendar-Middleware-thunk/requests";

export default  {
 

  requestEvents(startStr, endStr) {
    return (dispatch) => {
      return requestEventsInRange(startStr, endStr).then(
        (plainEventObjects) => {
          dispatch({
            type: "RECEIVE_EVENTS",
            plainEventObjects,
          });
        }
      );
    };
  },

   createEvent(plainEventObject) {
    return (dispatch) => {
      return requestEventCreate(plainEventObject).then(() => {
        dispatch({
          type: "CREATE_EVENT",
          plainEventObject: {
           
            ...plainEventObject,
          },
        });
      });
    };
  }, 
 /*  createEvent(plainEventObject) {
   return (dispatch)=>{
    console.log(plainEventObject)

      return ((plainEventObject) => {
        dispatch({
          
          type: "CREATE_EVENT",
          plainEventObject
        });
      });
    }
  },
 */
  updateEvent(plainEventObject) {
    return (dispatch) => {
      return requestEventUpdate(plainEventObject).then(() => {
        dispatch({
          type: "UPDATE_EVENT",
          plainEventObject,
        });
      });
    };
  },
};

